import React from 'react'
import StyleSheet from './ProjectStyles.module.css'

function ProjectBox(props) {
    const inputStyle ={
        'backgroundImage': `url(${props.img})`,
    }
    const rankClasss = props.rank == 1 ? 'box1' :
    props.rank == 2 ? 'box2' : props.rank == 3 ? 'box3' : props.rank == 4 ? 'box4' : '';

    return (
        <React.Fragment>
            <div style={inputStyle} className={`${StyleSheet.ProjectBoxContainer} box ${rankClasss}`}>
                <div className={StyleSheet.hoverBg}>
                    <h1 className="title">{props.title}</h1>
                    <p className="subtitle">{props.subtitle}</p>
                </div>
            </div>
        </React.Fragment>
    )
}

export default ProjectBox
