import React from 'react'
import { Link } from 'react-router-dom';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faBars} from '@fortawesome/free-solid-svg-icons'

const Header = props => {
    const inputStyles = {
        'backgroundColor':props.bgColor
    }
    return (
        <nav style={inputStyles} className={`navbar ${props.className}`} role="navigation" aria-label="main navigation" id={props.id}>
            {props.children}
        </nav>
    )
}

const Logo = props => {
    return (
        <div className={`${props.className} navbar-brand`} id={props.id}>
            <a className="navbar-item" href={props.href}>
                {props.children}
            </a>
            <a role="button" className="navbar-burger burger" aria-label="menu" aria-expanded="false" data-target="componentMenuBar">
                <FontAwesomeIcon icon={faBars}/>
            </a>
        </div>
    )
}

const MenuBar = props => {
    document.addEventListener('DOMContentLoaded', () => {
        const $navbarBurgers = Array.prototype.slice.call(document.querySelectorAll('.navbar-burger'), 0);
        if ($navbarBurgers.length > 0) {
            $navbarBurgers.forEach( el => {
                el.addEventListener('click', () => {
                    const target = el.dataset.target;
                    const $target = document.getElementById(target);
                    el.classList.toggle('is-active');
                    $target.classList.toggle('is-active');
                    if (props.onExpand) {
                        const object = props.onExpand;
                        for (let elementID in object) {
                            if (object.hasOwnProperty(elementID)) {
                                document.getElementById(elementID).classList.toggle(object[elementID]);
                            }
                        }
                    }
                });
            });
        }
    });
    const align = props.menuLeft ? "navbar-start":"navbar-end";

    return (
        <div id="componentMenuBar" class={`${props.className} navbar-menu`}>
            <div class={`${align}`}>
                {props.children}
            </div>
        </div>
    )
}

const Menu = props =>{
    const display = props.path ? 
    <Link class={`${props.className} navbar-item`} to={props.path} id={props.id}onClick={onClickHandler} >
        {props.name}
    </Link>
    : 
    <a class={`${props.className} navbar-item`} href={props.href} id={props.id} onClick={onClickHandler}>
        {props.name}
    </a>
    return display

    function onClickHandler() {
        const $navbarBurgers = Array.prototype.slice.call(document.querySelectorAll('.navbar-burger'), 0);
        if ($navbarBurgers.length > 0){
            $navbarBurgers.forEach( el => {
                el.click();
            });
        }
    }
}

export {Header,Logo,MenuBar,Menu} 
