import React from 'react';
import './App.css';
import {HashRouter,Switch,Route} from 'react-router-dom'
import {Header,Logo,MenuBar,Menu} from './components/Header/NavigationBar'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faGithub,} from '@fortawesome/free-brands-svg-icons'
import Home from './components/Home/Home'
import About from './components/About/About'
import Project from './components/projects/project'
import Contact from './components/Contact/Contact'
import Showcase from './components/Showcase/Showcase'

function App() {
  const expandClasses = {
    'componentMenuBar': 'siteMenubarMobStyle',
  }

  return (
    <div className="App">
      <HashRouter>
        <Header className="siteHeader container is-dark is-fluid is-fixed-top " id="siteHeaderId">
          <Logo className="siteLogo" href="https://develsanx.github.io">
            <FontAwesomeIcon icon={faGithub}/>
            <span>develSanx</span>
          </Logo>
          <MenuBar className="siteMenubar" onExpand={expandClasses}>
            <Menu name="Home" path="/"/>
            <Menu name="About" path="/about"/>
            <Menu name="Highlights" path="/highlights"/>
            <Menu name="Contact" path="/contact"/>
          </MenuBar>
        </Header>
        <Switch>
          <Route exact path="/" component={Home}/>
          <Route path="/about" component={About}/>
          <Route exact path="/highlights" component={Project}/>
          <Route path="/contact" component={Contact}/>
          <Route exact path="/projects" component={Showcase}/>
        </Switch>
      </HashRouter>
    </div>
  );
}

export default App;
