import React from 'react'
import StyleSheet from './ShowcaseStyles.module.css'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faGithubAlt} from '@fortawesome/free-brands-svg-icons'
import {faEye} from '@fortawesome/free-solid-svg-icons'
import placeholder from '../../assets/images/placeholder.jpg'

function ProjectCard(props) {

    const inputStyle ={
        'backgroundImage': `url(${props.img ? props.img : placeholder})`
    }

    return (
        <div className={StyleSheet.projectCard}>
            <div style={inputStyle} className={StyleSheet.cardBox}>
                <div className={StyleSheet.hoverBg}>
                    <a class="button is-light is-medium" href={props.site}>
                        <FontAwesomeIcon icon={faEye}/>
                        <span>View Project</span>
                    </a>
                    <a class="button is-light is-medium" href={props.code}>
                    <FontAwesomeIcon icon={faGithubAlt}/>
                        <span>Source Code</span>
                    </a>
                </div>
            </div>
            <h1 className={`${StyleSheet.title} title`}>
                {props.title}
            </h1>
            <p className={`${StyleSheet.subtitle} subtitle`}>
                {props.subtitle}
            </p>
        </div>
    )
}

export default ProjectCard
